<template>
  <div>
    <h2>Change message</h2>
    <input type="text" v-model="message" @keypress.enter="change">
    <button @click="change">change</button>
    <span>{{ output }}</span>
  </div>
</template>
<script>
export default {
  data() {
    return {
      message: '',
      output: 'default',
    }
  },
  methods: {
    change() {
      this.output = this.message;
      this.inputClear();
    },
    inputClear() {
      this.message = '';
    }
  }
}
</script>