<template>
  <div>
    <h2>Counter</h2>
    <button class="resetBtn" @click="reset">reset</button>
    <button @click="descrease">-</button>
    <button @click="increase">+</button>
    <span>{{ count }}</span>
  </div>
</template>
<script>
export default {
  //data: function() 축약가능
  data() {
    return {
      // 여기에 데이터 정의
      count: 0,
    }
  },
  // 함수 정의
  methods: {
    // 증가
    increase() {
      this.count++;
    },
    // 감소
    descrease() {
      this.count--;
    },
    // reset
    reset() {
      this.count = 0;
    }
  }
}
</script>
<style scoped>
  button {
    width: 35px;
    height: 35px;
  }
  .resetBtn {
    width: auto;
  }
</style>